// Dependencies
import React, { Component } from 'react'
// import { NavLink } from 'react-router-dom'
import { GoPrimitiveDot } from 'react-icons/go'
import Header from 'components/Base-Layout/Header'
import Chart1 from 'components/Home-Page/Chart1'
import Chart2 from 'components/Home-Page/Chart2'
import Chart3 from 'components/Home-Page/Chart3'
// Internals
import 'components/Home-Page/index.css'
// import img1 from 'assets/images/Arnawa-removebg-preview.png'

export default class index extends Component {
    constructor(props) {
      super(props);

      this.state = {
      
        series: [44, 55, 13],
        options: {
          chart: {
            width: 480,
            type: 'pie',
          },
          labels: ['Men', 'Women', 'Accessories'],
          responsive: [{
            breakpoint: 480,
            options: {
              chart: {
                width: 200
              },
              legend: {
                position: 'bottom'
              }
            }
          }]
        },
      
      
      };
    }

    render() {
        return (
            <div className="home">
                <div className="container">
                    {/* <h6><img src={img1} alt="img"></img><b>Arn</b>awa.</h6>
                    <h1>Overview</h1>
                    <h5>An overview of your interesting sales journey.</h5> */}
                    <Header title="Overview" tag="An overview of your interesting sales journey." />
                    <hr />
                    <div className="row text-center">
                        <div className="col a-home">
                            <h3>Payment Confirm</h3>
                            <h5>10</h5>
                        </div>
                        <div className="col b-home">
                            <h3>Payment Cancel</h3>
                            <h5>5</h5>
                        </div>
                        <div className="col c-home">
                            <h3>Payment Pending</h3>
                            <h5>8</h5>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col order-home">
                            <div className="table-responsive">
                                <table id="example" className="table table-borderless">
                                    <thead>
                                        <tr>
                                            <th>Order List</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                    <tr>
                                        <td className="text-center"><GoPrimitiveDot color={'yellow'}></GoPrimitiveDot></td>
                                        <td>Jonathan Red</td>
                                        <td>2</td>
                                        <td>SHR201</td>
                                        <td><b>Pending</b></td>
                                    </tr>
                                    <tr>
                                        <td className="text-center"><GoPrimitiveDot color={'yellow'}></GoPrimitiveDot></td>
                                        <td>Anthony Fuse</td>
                                        <td>1</td>
                                        <td>JKT301</td>
                                        <td><b>Pending</b></td>
                                    </tr>
                                    <tr>
                                        <td className="text-center"><GoPrimitiveDot color={'green'}></GoPrimitiveDot></td>
                                        <td>Kenin Jr.</td>
                                        <td>10</td>
                                        <td>SHR303</td>
                                        <td><b>Confirm</b></td>
                                    </tr>
                                    <tr>
                                        <td className="text-center"><GoPrimitiveDot color={'green'}></GoPrimitiveDot></td>
                                        <td>Kenin Jr.</td>
                                        <td>10</td>
                                        <td>JNS303</td>
                                        <td><b>Confirm</b></td>
                                    </tr>
                                    <tr className="cancel">
                                        <td className="text-center"><GoPrimitiveDot color={'red'}></GoPrimitiveDot></td>
                                        <td>Gery Issac</td>
                                        <td>3</td>
                                        <td>SHR203</td>
                                        <td><b>Cancel</b></td>
                                    </tr>
                                    <tr>
                                        <td className="text-center"><GoPrimitiveDot color={'green'}></GoPrimitiveDot></td>
                                        <td>Sun Tyson</td>
                                        <td>8</td>
                                        <td>JNS303</td>
                                        <td><b>Confirm</b></td>
                                    </tr>
                                    <tr>
                                        <td className="text-center"><GoPrimitiveDot color={'yellow'}></GoPrimitiveDot></td>
                                        <td>Yui Tse</td>
                                        <td>5</td>
                                        <td>SHR203</td>
                                        <td><b>Pending</b></td>
                                    </tr>
                                    <tr className="cancel">
                                        <td className="text-center"><GoPrimitiveDot color={'red'}></GoPrimitiveDot></td>
                                        <td>Reddick</td>
                                        <td>1</td>
                                        <td>SHR303</td>
                                        <td><b>Cancel</b></td>
                                    </tr>
                                    <tr>
                                        <td className="text-center"><GoPrimitiveDot color={'yellow'}></GoPrimitiveDot></td>
                                        <td>Michael Black</td>
                                        <td>1</td>
                                        <td>JKT103</td>
                                        <td><b>Pending</b></td>
                                    </tr>
                                    <tr>
                                        <td className="text-center"><GoPrimitiveDot color={'green'}></GoPrimitiveDot></td>
                                        <td>Adam Hirs</td>
                                        <td>3</td>
                                        <td>SHR303</td>
                                        <td><b>Confirm</b></td>
                                    </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                        <div className="col align-self-center" id="pie-categories">
                            <h3>Sales by Categories</h3>
                            <Chart1 />
                        </div>
                    </div>
                    <div className="row mb-5">
                        <div className="col">
                            <h3>Product Sales by Month</h3>
                            <Chart2 />
                        </div>
                        <div className="col">
                            <h3>Product Sales by Region</h3>
                            <Chart3 />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
